import { Link, useLocation } from "react-router-dom";
import Header from "../components/Header";
import './Complete.css';

const Complete = () => {

    const location = useLocation().state;

    const strings = [
        {
            id: 'complaintTitle',
            text: '  ',
        },
        {
            id: 'complaintBody',
            text: '       \n      \n       !',
        },
        {
            id: 'helpTitle',
            text: ' ',
        },
        {
            id: 'helpBody',
            text: '    .\n      !',
        },
    ];

    var title, body;

    if (location === 'complaint') {
        title = strings[0].text;
        body = strings[1].text;
    } else if (location === 'help') {
        title = strings[2].text;
        body = strings[3].text;
    }

    return (
        <>
            <Header />
            <div className="container">
                <div style={{ margin: '89px 24px 16px' }} className="Title Large-Title">
                    {title}
                </div>
                <div style={{ textAlign: 'center', color: 'var(--black-50)' }} className="Subtext Body-2">
                    {
                        body.split("\n").map((i,key) => {
                            return <div key={key}>{i}</div>;
                        })
                    }
                </div>
                <img style={{ width: '280px', height: '280px', marginTop: '88px' }} src='./images/app_symbol_3d.png' />
            </div>
            <Link to='/' style={{ textDecoration: "none", color: 'black' }}>
                <div className="btnHome">
                     
                </div>
            </Link>
            <div className="gradientBoxDown" />
        </>
    );
}

export default Complete;