/*global kakao*/
import React, { useEffect, useState } from 'react';
import { Link, useHistory, useLocation } from 'react-router-dom';
import Header from '../components/Header';
import { db } from '../firebase';
import { addDoc, serverTimestamp, GeoPoint, collection } from "firebase/firestore";
import './Complaint.css';


import Checkbox from '../components/Checkbox';


const Complaint = ({userInform}) => {
    const [message, setMessage] = useState('');
    const [address, setAdress] = useState('');
    const history = useHistory();

    const location = useLocation();
    console.log(location);

    useEffect(() => {
        if (sessionStorage.getItem("kakao_token") == null) {
            history.push('/login');
        }
    })


    useEffect(() => {
        var mapContainer = document.getElementById('map'), //   div
            mapOption = {
                draggable: false,
                center: new kakao.maps.LatLng(37.55634, 126.93635), //  
                level: 1 //   
            };

        //  
        var map = new kakao.maps.Map(mapContainer, mapOption);

        // 
        if (navigator.geolocation) {

            navigator.geolocation.getCurrentPosition(function (position) {

                //  
                var lat = position.coords.latitude,
                    lon = position.coords.longitude;
                var locPosition = new kakao.maps.LatLng(lat, lon);

                //   
                var markerCurrent = new kakao.maps.MarkerImage(
                    './images/ic_marker_current.svg',
                    new kakao.maps.Size(32, 32),
                    { offset: new kakao.maps.Point(0, 0) });

                var marker = new kakao.maps.Marker({
                    position: locPosition,
                    image: markerCurrent, //  
                    clickable: true
                });
                marker.setMap(map);
                map.setCenter(locPosition);

                // TODO: 1   
            });

        } else { // HTML5 GeoLocation    
            var message = '   .  .'
            alert(message)
        }
    }, [])

    const onChange = (event) => {
        const {
          target: { value },
        } = event;
        setMessage(value);
    };

    const sendComplaint = () => {
        const field = {
            timestamp: serverTimestamp(),
            address: address,
            who: sessionStorage.getItem("uid"),
            message: message
        };
        
        if (message === "") {
            window.confirm("  .")
        } else {
            addDoc(collection(db, "help", "help", "compaint"), field);
            history.push('/complete');
        }
    }

    const cancelConfirm = () => console.log("")

    const useConfirm = (onConfirm, onCancel, message = "Are you sure?") => {
        if (!onConfirm && typeof onConfirm !== "function") {
            return;
        }

        if (!onCancel && typeof onCancel !== "function") {
            return;
        }

        const confirmAction = () => {
            if (window.confirm(message)) {
                onConfirm();

            } else {
                onCancel();
            }
        };

        return confirmAction;
    };

    const complaintConfirm = useConfirm(sendComplaint, cancelConfirm, " ?");

    return (
        <>
            <Header />
            <div style={{ margin: '32px 16px' }}>
                <div className='Title Large-Title'>
                     
                </div>
                <div style={{ marginTop: '16px', color: 'var(--black-50)' }} className='Subtext Body-2'>
                    , ,      .<br />
                           .
                </div>
            </div>
            <div>
                
                {/*     */}
                <div className='text Headline' style={{ marginBottom: '12px' }}>
                    {/*   */}
                    {/* <input type='checkbox' className="check-position" onClick={e=> changeState(e)} checked={isCheckingBox}></input><span className="text-position"></span> */}
                </div>
                <Checkbox text="" data={address} setData={setAdress} />
             
                
            </div>
            <div style={{ margin: '32px 16px' }}>
               
                {/*  */}
                <div id="map" style={{ height: "0", paddingBottom: '0%' }}></div>

                <div className='text Headline' style={{ marginTop: '24px' }}></div>
                <textarea 
                    style={{ whiteSpace: 'pre-wrap' }}
                    className='message -Placeholder Placeholder-2'
                    onChange={onChange}
                    placeholder='
                  . &#13;
                  ,     . &#13;
                  /       . &#13; &#13;
                  &#13;
                     . &#13;
                    . &#13;
                    .'>
                </textarea>
            </div>

                <div className='btnSubmit \- Large-Lable' onClick={complaintConfirm}>
                     
                </div>
        </>
    );
}

export default Complaint;