import React, { useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import styles from './MypageDetail.module.css';
import { auth, db } from '../firebase';
import { deleteUser } from 'firebase/auth';
import { doc, deleteDoc } from 'firebase/firestore';

const MypageDetail = ({ userInform, setUserInform }) => {
    const history = useHistory();

    useEffect(() => {
        if (sessionStorage.getItem("kakao_token") == null) {
            history.push('/login');
        }
    }, []);
    
    const useConfirm = (onConfirm, onCancel, message = "Are you sure?") => {
        if (!onConfirm && typeof onConfirm !== "function") {
            return;
        }

        if (!onCancel && typeof onCancel !== "function") {
            return;
        }

        const confirmAction = () => {
            if (window.confirm(message)) {

                onConfirm();

            } else {
                onCancel();
            }
        };

        return confirmAction;
    };
    const applyConfirmLog = () => {
        
        console.log("")
        alert('.');
        logout();
    }
    const applyConfirmUnlink = () => {
        
        console.log("")
        alert('.  .');
        unlink();
    }
    const cancelConfirm = () => console.log("")

    const confirmLog = useConfirm(
        applyConfirmLog,
        cancelConfirm,
        "?"
    );

    const confirmExit = useConfirm(
        applyConfirmUnlink,
        cancelConfirm,
        "  ?"
    )

    const logout = () => {
        fetch("https://kapi.kakao.com/v1/user/logout",{
            method: "GET",
            headers: {
                'Authorization' : `Bearer ${sessionStorage.getItem("kakao_token")}`,
                'content-type' : 'application/x-www-form-urlencoded;charset=utf-8'
            },
        })
        auth.signOut();
        setUserInform(null);
        
        sessionStorage.removeItem("kakao_token");
        history.push("/");
    }

    const unlink = () => {
        confirmExit();
        fetch("https://kapi.kakao.com/v1/user/unlink",{
            method: "GET",
            headers: {
                'Authorization' : `Bearer ${sessionStorage.getItem("kakao_token")}`,
                'content-type' : 'application/x-www-form-urlencoded;charset=utf-8'
            },
        })
        const user = auth.currentUser;
        deleteUser(user);
        const userRef = doc(db, "user", userInform.uid);
        deleteDoc(userRef);
        setUserInform(null);
        
        sessionStorage.removeItem("kakao_token");
        history.push("/");
    }

    return (
        <>
            <Link to="/mypage" style={{ textDecoration: 'none', color: 'black' }}>
                <img style={{
                    position: 'fixed',
                    top: '16px',
                    left: '16px',
                    width: '24px',
                    height: '24px',
                }}
                    src="../images/ic_arrow_back.svg" />
            </Link>
            <div className={styles.headerContainer}>
                <span className="text Headline"> </span>
            </div>
            <div className={styles.profile}>
                <img src={userInform.photoURL} alt="profile" />
            </div>
            <div className={styles.profileContainer}>
                <div className={styles.title}>
                     
                </div>
                <div className={styles.mypage_info}>
                    <div className={styles.sub_title}>
                        
                    </div>
                    <div className={styles.content}>
                        {userInform.displayName}
                    </div>
                    <div className={styles.profileLine} />
                </div>
                <div className={styles.mypage_info}>
                    <div className={styles.sub_title}>
                        
                    </div>
                    <div className={styles.content}>
                        {userInform.email.slice(-13) === '@sovement.com' ? '  .' : userInform.email}
                    </div>
                    <div className={styles.profileLine} />
                </div>
            </div>
            <div className={styles.mypage_tab}>
                <div className="log-out"><div className={styles.tab} onClick={confirmLog}><span></span></div></div>
                <div className={styles.mypageLine} />
                <div><div className={styles.tab} onClick={confirmExit}></div></div>
            </div>
        </>
    );
}

export default MypageDetail;